<template>
  <div class="app h-screen">
    <div class="flex flex-col justify-center items-center h-screen">
      <label
        class="w-64 mb-5 flex flex-col items-center px-2 py-4 bg-gray-400 rounded shadow-lg tracking-wide uppercase cursor-pointer hover:bg-gray-500"
      >
        <svg
          class="w-8 h-8"
          fill="currentColor"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 20 20"
        >
          <path
            d="M16.88 9.1A4 4 0 0 1 16 17H5a5 5 0 0 1-1-9.9V7a3 3 0 0 1 4.52-2.59A4.98 4.98 0 0 1 17 8c0 .38-.04.74-.12 1.1zM11 11h3l-4-4-4 4h3v3h2v-3z"
          />
        </svg>
        <span class="mt-2 text-base leading-normal">Select a file</span>
        <input
          type="file"
          class="hidden"
          id="fileDataTarget"
          ref="myFiles"
          @click="clearFileValue"
          @change="previewFiles"
          accept=".xls, .xlsx"
        />
      </label>
      <div v-if="initialData.length > 0">
        <table id="my-table" class="table-auto" v-show="false">
          <thead class="bg-blue-500 text-white">
            <tr>
              <th>RANKING</th>
              <th>NISN</th>
              <th>NAMA</th>
              <th>TOTAL NILAI</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(data, index) in initialData" :key="data.NAMA">
              <td>{{ index+1 }}</td>
              <td>{{ data.NISN }}</td>
              <td>{{ data.NAMA }}</td>
              <td>{{ data.TOTAL }}</td>
            </tr>
          </tbody>
        </table>

        <button
          @click="download"
          class="bg-gray-400 hover:bg-gray-500 text-grey-darkest font-bold py-3 px-6 rounded inline-flex items-center"
        >
          <svg class="w-4 h-4 mr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
            <path d="M13 8V2H7v6H2l8 8 8-8h-5zM0 18h20v2H0v-2z" />
          </svg>
          <span>Download</span>
        </button>
      </div>
    </div>
    <div class="row">
      <div class="col-md-9">
        <div v-if="isLoading" class="divLoader">
          <svg class="svgLoader" width="10em" height="10em" viewBox="0 0 100 100">
            <path
              ng-attr-d="config.pathCmd"
              ng-attr-fill="config.color"
              stroke="none"
              d="M10 50A40 40 0 0 0 90 50A40 42 0 0 1 10 50"
              fill="#51CACC"
              transform="rotate(179.719 50 51)"
            >
              <animateTransform
                attributeName="transform"
                type="rotate"
                calcMode="linear"
                values="0 50 51;360 50 51"
                keyTimes="0;1"
                dur="1s"
                begin="0s"
                repeatCount="indefinite"
              />
            </path>
          </svg>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import jsPDF from "jspdf";
import "jspdf-autotable";

export default {
  name: "HelloWorld",
  data() {
    return {
      isLoading: false,
      initialData: []
    };
  },
  methods: {
    previewFiles() {
      this.files = this.$refs.myFiles.files[0];
      if (this.isAllowed(this.files)) {
        this.isLoading = true;
        let reader = new FileReader();
        reader.readAsBinaryString(this.files);

        reader.onload = function() {
          let data = reader.result;
          this.loadData(data);
        }.bind(this);

        reader.onerror = function() {
          this.$swal({
            icon: "error",
            title: "Terjadi Kesalahan",
            text: "Berkas tidak dapat dibaca!"
          });
        };
      } else {
        this.$swal({
          icon: "error",
          title: "Terjadi Kesalahan",
          text: "Silahkan pilih file dengan ekstensi .xlsx atau .xls"
        });
      }
    },
    isAllowed(file) {
      let filePath = file.name;
      let allowedExtensions = /(\.xls|\.xlsx)$/i;
      return allowedExtensions.exec(filePath) ? true : false;
    },

    clearFileValue(e) {
      e.target.value = "";
    },

    async loadData(data) {
      let workbook = XLSX.read(data, {
        type: "binary"
      });
      try {
        let XL_row_object = XLSX.utils.sheet_to_row_object_array(
          workbook.Sheets["Worksheet"],
          { blankrows: false, defval: "" }
        );

        this.initialData = await XL_row_object.map(data => {
          return {
            NISN: data["NISN"].trim(),
            NAMA: data["Nama"].trim(),
            TOTAL: data["Total Pengetahuan"] + data["Total Skill"]
          };
        }).slice(1);

        this.initialData.sort(function(a, b) {
          return b.TOTAL - a.TOTAL;
        });
        await Promise.all(this.initialData);
      } catch (e) {
        this.$swal({
          icon: "error",
          title: "Terjadi Kesalahan",
          text: e.message
        });
      }
      this.isLoading = false;
    },

    download() {
      const doc = new jsPDF();
      doc.autoTable({ html: "#my-table" });
      doc.save("ranking.pdf");
    }
  }
};
</script>

